.services {
    padding-top: var(--section-offset);
    padding-bottom: 180px;

    &__title {
        margin: 0;
        margin-bottom: 26px;
        font-weight: 700;
        font-size: 32px;
        line-height: 150%;
        color: var(--color-gray900);
    }

    &__article {
        margin-bottom: 48px;
        font-size: 16px;
        line-height: 160%;
        color: var(--color-gray700);
    }

    &__btn {
        display: block;
        width: 160px;
        font-size: 14px;
        line-height: 44px;
        letter-spacing: 0.5px;
        text-align: center;
        color: var(--color-primary);
    }

    &__item {
        @include flex-all-sb;
        flex-direction: row;

        &:not(:last-child) {
            margin-bottom: 120px;
        }

        &:nth-child(2n) {
            flex-direction: row-reverse;

            @include small-tablet {
                flex-direction: column;
            }
        }

        @include small-tablet {
            flex-direction: column;
        }
    }

    &__img {
        width: 575px;

        @include big-tablet {
            width: 45%;
        }

        @include small-tablet {
            width: 100%;
            margin-bottom: 15px;
        }
    }

    &-item__content {
        width: 575px;

        @include big-tablet {
            width: 45%;
        }

        @include small-tablet {
            width: 100%;
        }
    }
}