.contacts {
  padding-top: 122px;
  padding-bottom: var(--section-offset);
}

.contacts-content {
  display: flex;

  &__img {
    display: block;
    width: 100%;
    max-width: 495px;
    margin-right: 135px;
    border-radius: 4px;
  }

  &__form {
    width: 100%;
    max-width: 600px;
  }

}

.contacts-form {
  &__flex {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__field {
    width: 47.5%;
    margin-bottom: 24px;
  }

  &__caption {
    display: block;
    margin-bottom: 8px;
    font-size: 16px;
    line-height: 160%;
    color: var(--color-gray800);
  }

  &__input {
    width: 100%;
    height: 52px;
    padding-left: 16px;
    font-size: 16px;
    line-height: 160%;
    color: var(--color-gray800);
  }

  .choices__inner {
    height: 52px;
    border-radius: 4px;
    border-color: var(--color-gray400);
    font-size: 16px;
    line-height: 160%;
    color: var(--color-gray800);
    background-color: var(--color-gray200);
  }

  .choices__placeholder {
    opacity: 1;
    color: var(--color-gray600);
  }

  &__textarea {
    width: 100%;
    height: 128px;
    margin-bottom: 38px;
    padding: 16px 0 0 16px;
    color: var(--color-gray800);
    resize: none;
  }

  &__btn {
    width: 210px;
    height: 52px;
    letter-spacing: 0.5px;
  }
}