.offices {
  padding-top: 80px;
  background-color: var(--color-gray200);

  &__list {
    --offices-gap: 120px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: calc(var(--offices-gap) * 2);
  }

  &__item {
    position: relative;
    @include flex-column;
    align-items: flex-start;
    grid-column: 1 span;

    &:not(:last-child):after {
      content: '';
      position: absolute;
      top: 0;
      right: calc(-1 * var(--offices-gap));
      display: block;
      width: 2px;
      height: 254px;
      background-image: url('../img/svg/line.svg');
      @include bg-cover;
    }
  }
}

.offices {
  &__subtitle {
    margin: 0;
    margin-bottom: 24px;
    font-weight: 700;
    font-size: 28px;
    line-height: 150%;
    color: var(--color-gray900);
  }

  &__adress {
    display: block;
    margin-bottom: 10px;
    font-size: 16px;
    line-height: 160%;
    color: var(--color-gray900);
  }

  &__link {
    margin-bottom: 25px;
    border-bottom: 1px solid currentColor;
    font-size: 16px;
    line-height: 1;
    color: var(--color-primary);
    transition: color 0.25s ease-in-out;

    @include hover{
      color: var(--color-primary-hover);
    }
  }

  &__bottom{
    font-size: 16px;
    line-height: 160%;
    color: var(--color-gray700);
  }
}

.offices-bottom {
  &__element:not(:last-child) {
    margin-bottom: 4px;
  }

  &__value {
    color: var(--color-gray900);

    &--link {
      transition: color 0.25s ease-in-out;

      @include hover {
        color: var(--color-primary);
      }
    }
  }
}