// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Ubuntu", sans-serif;
  --content-width: 1230px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  
  --section-offset: 120px;

  // colors
  --color-black: #000;
  --color-light: #fff;
  --color-primary: #ff5a30;
  --color-primary-hover: #fc3300;
  --color-gray100: #f9f9f9;
  --color-gray200: #f4f5f6;
  --color-gray300: #e5e8ed;
  --color-gray400: #d7dadd;
  --color-gray500: #b3b7bc;
  --color-gray600: #9a9ca5;
  --color-gray700: #787a80;
  --color-gray800: #424551;
  --color-gray900: #1e212c;

  // shadows
  --shadow-small: 0px 80px 80px -20px rgba(154, 156, 165, 0.08), 0px 30px 24px -10px rgba(154, 156, 165, 0.05), 0px 12px 10px -6px rgba(154, 156, 165, 0.04), 0px 4px 4px -4px rgba(30, 33, 44, 0.03);

}
