.partners-about {
  padding-top: var(--section-offset);
  padding-bottom: 180px;

  &__list {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    column-gap: 102px;
    row-gap: 33px;

    @include big-tablet {
      grid-template-columns: repeat(2, 1fr);
    }
  }

  &__item {
    grid-column: 1 span;
    grid-row: 1 span;
    margin: 0 auto;
  }

  &__link {
    display: block;

    &:focus {
      border-radius: 4px;
      outline: 2px solid var(--color-primary);
      outline-offset: 2px;
    }
  }
}