.about-section {
    padding-top: var(--section-offset);
    padding-bottom: 60px;

    &__video-wrapper {
        position: relative;
        width: 100%;
        height: 500px;

        @include mobile {
            height: 300px;
        }
    }

    &__blackout {
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 100%;
        height: 100%;
        background-color: var(--color-gray900);
        opacity: 0.6;
        transition: opacity 0.25s ease-in-out, visibility 0.25s ease-in-out;

        &.disable {
            opacity: 0;
            visibility: hidden;
        }
    }

    &__video {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__play-btn {
        position: absolute;
        top: 50%;
        left: 50%;
        @include flex-all-center;
        width: 80px;
        height: 80px;
        border-radius: 50%;
        background-color: var(--color-primary);
        fill: var(--color-light);
        transform: translate(-50%, -50%);
        z-index: 15;
        transition: background-color 0.25s ease-in-out, visibility 0.25s ease-in-out, opacity 0.25s ease-in-out;

        &.disable {
            opacity: 0;
            visibility: hidden;
        }

        @include hover {
            background-color: var(--color-primary-hover);
        }

        &:focus {
            outline: 2px solid var(--color-primary);
            outline-offset: 2px;
        }
    }

    &__play-ico {
        width: 24px;
        height: 24px;
        margin-left: 4px;
        fill: inherit;
    }
}