.partners {
    margin-top: var(--section-offset);
    padding-top: 81px;
    padding-bottom: 95px;
    background-color: var(--color-gray200);

    &__title {
        margin-bottom: 60px;
    }

    &__list {
        display: grid;
        grid-template-columns: repeat(6, 1fr);
        gap: 102px;

        @include big-tablet {
            grid-template-columns: repeat(2, 1fr);
        }
    }

    &__item {
        grid-column: 1 span;
        margin: 0 auto;
    }

    &__link {
        display: block;

        &:focus {
            outline: 2px solid var(--color-primary);
            outline-offset: 2px;
        }
    }
}

.portfolio-partners {
    padding-top: 180px;
    padding-bottom: 115px;
    background-color: var(--color-light);
}