.mission {
    padding-top: var(--section-offset);

    &__list {
        --mission-gap: 94px;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: calc(var(--mission-gap) * 2);

        @include tablet {
            --mission-gap: 45px;
        }

        @include small-tablet {
            --mission-gap: 10px;
        }

        @include mobile {
            grid-template-columns: repeat(12, 1fr);
        }
    }

    &__item {
        position: relative;
        padding-top: 72px;
        text-align: center;
        background-size: 48px 48px;
        background-repeat: no-repeat;
        background-position: top center;

        @include mobile {
            grid-column: 12 span;
        }

        &--quality {
            background-image: url('../img/svg/quality.svg');
        }
        
        &--safety {
            background-image: url('../img/svg/safety.svg');
        }
        
        &--comfort {
            background-image: url('../img/svg/comfort.svg');
        }

        &:not(:last-child)::after {
            content: '';
            position: absolute;
            top: 0;
            right: calc(-1 * var(--mission-gap));
            display: block;
            width: 2px;
            height: 188px;
            background-image: url('../img/svg/line.svg');

            @include mobile {
                display: none;
            }
        }
    }

    &__title {
        margin: 0;
        margin-bottom: 8px;
        font-weight: 700;
        font-size: 20px;
        line-height: 150%;
        color: var(--color-gray900);
    }

    &__descr {
        margin: 0;
        font-size: 16px;
        line-height: 160%;
        color: var(--color-gray700);
    }

    &-form {
        position: relative;
        top: 93px;
        width: 100%;
        padding: 48px 60px;
        border-radius: 4px;
        box-shadow: var(--shadow-small);
        background-color: var(--color-light);

        @include mobile {
            padding: 30px 15px;
        }

        &__title {
            margin-bottom: 32px;
            font-weight: 700;
            font-size: 28px;
            line-height: 150%;
            text-align: center;

            @include mobile {
                font-size: 24px;
            }
        }

        &__content {
            @include flex-all-sb;
            align-items: flex-end;

            @include tablet {
                flex-wrap: wrap;
            }
        }

        &__label {
            @include flex-column;
            min-width: 260px;
            margin-right: 24px;

            &--big {
                flex-grow: 1;

                @include tablet {
                    width: 49%;
                    flex-grow: initial;
                }
            }

            @include tablet {
                width: 49%;
                margin-right: 0;
                margin-bottom: 20px;
            }

            @include small-tablet {
                width: 100%;
            }
        }

        &__designation {
            margin-bottom: 8px;
            font-size: 14px;
            line-height: 150%;
            color: var(--color-gray800);
        }

        &__input {
            width: 100%;
            height: 44px;
            padding-left: 16px;
            font-size: 14px;
        }

        &__btn {
            width: 104px;
            height: 44px;
            font-weight: 700;
            font-size: 14px;
            letter-spacing: 0.5px;

            @include tablet {
                width: 49%;
                margin-bottom: 20px;
            }

            @include small-tablet {
                width: 100%;
            }
        }
    }
}