.find-us {
  padding: var(--section-offset) 0;
  background-color: var(--color-gray200);

  &__list {
    @include flex-all-center;
    margin-top: 40px;
  }

  &__item {
    display: block;

    &:not(:last-child) {
      margin-right: 24px;
    }
  }

  &__link {
    display: block;
    width: 32px;
    height: 32px;
    fill: var(--color-gray700);

    @include hover {
      fill: var(--color-primary-hover);
    }
  }

  &__ico {
    width: 32px;
    height: 32px;
    fill: inherit;
    transition: fill 0.25s ease-in-out;
  }
}