.services-section {
    background-image: url(../img/services-section-bg.jpg);
    padding-top: 214px;
    padding-bottom: 80px;

    &__list {
        display: grid;
        grid-template-columns: repeat(12, 1fr);
        gap: 30px;

        @include mobile {
            grid-template-columns: repeat(6, 1fr);
        }
    }


    &__item {
        position: relative;
        grid-column: 3 span;

        @include tablet {
            grid-column: 6 span;
        }
    }

    &__link {
        position: relative;
        display: block;
        padding: 90px 40px;
        border-radius: 4px;
        box-shadow: var(--shadow-small);
        background-color: var(--color-light);
        fill: var(--color-primary);
        color: var(--color-gray900);

        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            display: block;
            width: 100%;
            height: 100%;
            @include bg-cover;
            background-image: url(../img/service-item.jpg);
            opacity: 0;
            transition: opacity 0.25s ease-in-out;
        }

        &::after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            display: block;
            width: 100%;
            height: 100%;
            border-radius: inherit;
            background-color: var(--color-gray900);
            opacity: 0;
            transition: opacity 0.25s ease-in-out;
        }

        @include hover {
            fill: var(--color-light);
            color: var(--color-light);
            &::before {
                opacity: 1;
            }
            &::after {
                opacity: 0.6;
            }
        }

        &:focus {
            outline: 2px solid var(--color-primary);
            outline-offset: 2px;
        }
    }

    &__content {
        position: relative;
        @include flex-column-center;
        z-index: 10;
    }

    &__ico {
        width: 52px;
        height: 52px;
        margin-bottom: 24px;
        fill: inherit;
        transition: fill 0.25s ease-in-out;
    }

    &__designation {
        font-weight: 700;
        font-size: 20px;
        line-height: 150%;
        color: inherit;
        transition: color 0.25s ease-in-out;

        @include big-tablet {
            font-size: 18px;
        }

        @include tablet {
            font-size: 16px;
        }
    }
}