html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.btn-basic--transparent {
  border: 1px solid currentColor;
  border-radius: 4px;
  font-weight: 700;
  color: var(--color-light);
  text-transform: uppercase;
  cursor: pointer;
  transition: background-color 0.25s ease-in-out, border-color 0.25s ease-in-out, color 0.25s ease-in-out;

  &:focus {
    outline: 2px solid var(--color-primary);
    outline-offset: 2px;
  }

  @include hover {
    border-color: var(--color-primary);
    color: var(--color-light);
    background-color: var(--color-primary);
  }
}

.btn-basic--primary {
  border: 1px solid var(--color-primary);
  border-radius: 4px;
  font-weight: 700;
  color: var(--color-light);
  text-transform: uppercase;
  background-color: var(--color-primary);
  cursor: pointer;
  transition: background-color 0.25s ease-in-out, border-color 0.25s ease-in-out;

  &:focus {
    outline: 2px solid var(--color-primary);
    outline-offset: 2px;
  }

  @include hover {
    border-color: var(--color-primary-hover);
    background-color: var(--color-primary-hover);
  }
}

.title {
  margin: 0;
  margin-bottom: 24px;
  font-weight: 700;
  font-size: 46px;
  line-height: 130%;
  color: var(--color-gray900);

  &--center {
    text-align: center;
  }

  @include mobile {
    font-size: 28px;
  }
}

.descr {
  display: block;
  margin-bottom: 60px;
  font-size: 18px;
  line-height: 150%;
  color: var(--color-gray700);

  &--center {
    text-align: center;
  }

  @include mobile {
    margin-bottom: 30px;
    font-size: 16px;
  }
}

.input {
  margin: 0;
  padding: 0;
  border: 1px solid var(--color-gray400);
  border-radius: 4px;
  color: var(--color-gray900);
  background-color: var(--color-gray200);

  &::placeholder {
    color: var(--color-gray600);
  }

  &:focus {
    outline: 2px solid var(--color-primary);
    outline-offset: 2px;
  }
}

.textarea {
  margin: 0;
  padding: 0;
  border: 1px solid var(--color-gray400);
  border-radius: 4px;
  color: var(--color-gray900);
  background-color: var(--color-gray200);

  &::placeholder {
    color: var(--color-gray600);
  }

  &:focus {
    outline: 2px solid var(--color-primary);
    outline-offset: 2px;
  }
}

.learn-more {
  @include flex-all-center;
  margin-top: 79px;

  @include small-tablet {
    @include flex-column;
    align-items: flex-start;
  }

  &__title {
    margin: 0;
    margin-right: 40px;
    font-weight: 700;
    font-size: 28px;
    line-height: 150%;
    color: var(--color-gray900);

    @include small-tablet {
      margin-bottom: 20px;
    }
  }

  &__btn {
    padding: 0 39px;
    font-weight: 700;
    font-size: 16px;
    line-height: 50px;
    letter-spacing: 0.5px;
    z-index: 1;
  }
}

.swiper-button-disabled {
  fill: var(--color-gray600) !important;
  background-color: transparent !important;
  cursor: default !important;
}

.custom-checkbox {
  width: 315px;
  user-select: none;
  cursor: pointer;

  &__input {
    position: absolute;
    opacity: 0;

    &:focus {
      outline: 2px solid var(--color-primary);
      outline-offset: 2px;
    }

     &:checked + .custom-checkbox__text::before {
       border-color: var(--color-primary);
       background-color: var(--color-primary);
       background-image: url("../img/svg/check.svg");
       background-repeat: no-repeat;
       background-position: center;
       background-size: 10px 8px;
     }
  }

  &__text {
    position: relative;
    display: inline-block;
    padding-left: 28px;
    font-size: 16px;
    line-height: 160%;
    color: var(--color-gray800);

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 4px;
      width: 16px;
      height: 16px;
      border: 1px solid var(--color-gray500);
      border-radius: 3px;
      background-color: var(--color-light);
      transition: border 0.25s ease-in-out, background-color 0.25s ease-in-out, background-image 0.25s ease-in-out;
    }
  }
}

.burger {
  --burger-width: 40px;
  --burger-height: 20px;
  --burger-line-height: 2px;

  position: absolute;
  right: 15px;
  top: 23px;
  z-index: 1000;
  display: none;
  border: none;
  padding: 0;
  width: var(--burger-width);
  height: var(--burger-height);
  color: #000;
  background-color: transparent;
  cursor: pointer;

  @include tablet {
    display: block;
  }
}

.burger::before,
.burger::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}

.burger::before {
  top: 0;
}

.burger::after {
  top: calc(100% - var(--burger-line-height));
}

.burger__line {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  transform: translateY(-50%);
  transition: transform 0.3s ease-in-out;
}

.burger--active::before {
  top: 50%;
  transform: rotate(45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}

.burger--active::after {
  top: 50%;
  transform: rotate(-45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}

.burger--active .burger__line {
  transform: scale(0);
  transition: transform 0.3s ease-in-out;
}

.burger:focus::after {
  opacity: 1;
}

.overlay {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 500;
  background-color: rgba(#000, 0.6);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

  &--active {
    opacity: 1;
    visibility: visible;
  }
}