.reviews {
    padding-top: 82px;
    background-color: var(--color-gray200);

    &-container {
        @include flex-sb;
    }

    &__title {
        margin-bottom: 61px;
    }

    &__content {
        width: 100%;
        max-width: 612px;
    }

    &__swiper {
        position: relative;
        padding-bottom: 13px;
    }

    &__avatar {
        width: 72px;
        height: 72px;
        margin-bottom: 21px;
        border-radius: 50%;
    }

    &__descr {
        margin: 0;
        margin-bottom: 24px;
        font-size: 16px;
        line-height: 160%;
        color: var(--color-gray800);
    }

    &__autor {
        display: block;
        margin-bottom: 4px;
        padding-right: 110px;
        font-weight: 700;
        font-size: 16px;
        line-height: 160%;
        color: var(--color-gray900)
    }

    &__position {
        padding-right: 110px;
        font-size: 14px;
        line-height: 150%;
        color: var(--color-gray700);
    }

    &__arrows {
        position: absolute;
        bottom: 0;
        right: 0;
        display: flex;
        z-index: 10;
    }

    &__arrow {
        @include flex-all-center;
        width: 48px;
        height: 48px;
        padding: 0;
        border: none;
        border-radius: 50%;
        background-color: transparent;
        fill: var(--color-black);
        cursor: pointer;
        transition: background-color 0.25s ease-in-out;

        &-ico {
            width: 24px;
            height: 24px;
            fill: inherit;
            transition: fill 0.25s ease-in-out;
        }

        @include hover {
            fill: var(--color-light);
            background-color: var(--color-primary);
        }
    }

    &__img {
        margin-bottom: -60px;
        border-radius: 4px;

        @include big-tablet {
            width: 300px;
        }

        @include tablet {
            display: none;
        }
    }
}