.facts {
    position: relative;
    padding-top: 180px;
    padding-bottom: 182px;
    overflow: hidden;

    &::before {
        content: '';
        position: absolute;
        top: -247px;
        left: -5px;
        display: block;
        width: 570px;
        height: 1065px;
        background-image: url('../img/facts-bg.png');
        background-repeat: no-repeat;
        background-position: left top;
        background-size: cover;
    }
    
    &__title {
        margin-bottom: 60px;
    }

    &__list {
        @include flex-sb;
        width: 100%;
        max-width: 736px;
        margin: 0 auto;

        @include small-tablet {
            @include flex-column-center;
        }
    }

    &__item {
        &:not(:last-child) {
            @include small-tablet {
                margin-bottom: 30px;
            }
        }
    }

    &__circle {
        position: relative;
        width: 148px;
        height: 148px;
        margin-bottom: 20px;
    }

    &__value {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-weight: 700;
        font-size: 32px;
        line-height: 150%;
        color: var(--color-gray900);
    }

    &__descr {
        display: block;
        text-align: center;
        font-size: 14px;
        line-height: 150%;
        color: var(--color-gray900);
    }
}

.circle-anim {
    transform: rotate(-90deg);
}

.progress-bg {
    opacity: 0.3;
}