.history {
  background-color: var(--color-gray200);
  padding-bottom: 100px;
}

.history-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 37px;

  &__buttons {
    position: relative;
    top: -11px;
    left: 12px;
    display: flex;
    align-items: center;
    user-select: none;
  }

  &__button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 48px;
    height: 48px;
    border: none;
    border-radius: 50%;
    background-color: transparent;
    fill: var(--color-black);
    cursor: pointer;
    transition: fill 0.25s ease-in-out, background-color 0.25s ease-in-out;

    @include hover {
      background-color: var(--color-primary);
      fill: var(--color-light);
    }

    &--disable {
      pointer-events: none;
      fill: var(--color-gray700);
    }
  }

  &__ico {
    width: 24px;
    height: 24px;
  }
}

.history-content {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 30px;

  @include small-tablet {
  grid-template-columns: repeat(6, 1fr);
  }

  &__nav {
    position: relative;
    grid-column: 4 span;
    @include flex-column;
    justify-content: space-between;

    &::before {
      content: "";
      position: absolute;
      left: 3px;
      display: block;
      width: 1px;
      height: 100%;
      background-color: var(--color-gray600);
    }

    @include small-tablet {
      grid-column: 6 span;
    }
  }

  &__slider {
    grid-column: 8 span;
    width: 100%;

    @include small-tablet {
      grid-column: 6 span;
    }
  }

  &__img {
    margin-bottom: 36px;
    border-radius: 4px;
  }

  &__text {
    margin: 0;
    font-size: 16px;
    line-height: 160%;
    color: var(--color-gray800);
  }
}

.history-nav__item {
    position: relative;
    padding-left: 41px;
    font-weight: 700;
    font-size: 28px;
    line-height: 150%;
    color: var(--color-gray600);
    cursor: pointer;
    user-select: none;
    transition: color 0.25s ease-in-out;

    @include tablet {
      font-size: 22px;
    }

    &--active {
      color: var(--color-primary);
      cursor: default;
    }

    &:not(:last-child) {
      margin-bottom: 20px;
    }

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      display: block;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background-color: currentColor;
      transform: translateY(-50%);
    }
}