.portfolio-section {
    margin-top: var(--section-offset);

    &__header {
        @include flex-sb;
        align-items: flex-end;
        flex-wrap: wrap;
        margin-bottom: 60px;
    }

    &__title {
        margin: 0;
        width: 705px;
    }

    &__arrows {
        position: relative;
        top: 12px;
        display: flex;
        z-index: 15;
        transform: translateX(12px);
        user-select: none;
    }

    &__arrow {
        @include flex-all-center;
        width: 48px;
        height: 48px;
        border: none;
        border-radius: 50%;
        background-color: transparent;
        fill: var(--color-black);
        cursor: pointer;
        transition: fill 0.25s ease-in-out, background-color 0.25s ease-in-out;

        &-ico {
            width: 24px;
            height: 24px;
            fill: inherit;
        }

        @include hover {
            fill: var(--color-light);
            background-color: var(--color-primary);
        }

        &--disable {
            pointer-events: none;
            fill: var(--color-gray700);
        }
    }

    &__swiper {
        margin: -100px;
        padding: 100px;
    }
    
    &__slide {
        display: block;
        border-radius: 4px;
        box-shadow: var(--shadow-small);
        overflow: hidden;
        transition: transform 0.25s ease-in-out, visibility 0.25s ease-in-out, opacity 0.25s ease-in-out;

        @include hover {
            .portfolio-section__btn {
                margin-bottom: 24px!important;
            }
        }

        &:focus {
            outline: 2px solid var(--color-primary);
            outline-offset: 2px;
        }
        
        &--hidden {
            visibility: hidden;
            opacity: 0;
        }
    }

    &__img {
        width: 100%;
        margin-bottom: 13px;
    }

    &__slide-title {
        margin: 0;
        margin-bottom: 4px;
        font-weight: 700;
        font-size: 20px;
        line-height: 150%;
        color: var(--color-gray900);
        text-align: center;
    }

    &__descr {
        display: block;
        margin-bottom: 24px;
        font-size: 14px;
        line-height: 150%;
        color: var(--color-gray700);
        text-align: center;
    }

    &__btn {
        width: 172px;
        margin: 0 auto;
        margin-bottom: -47px;
        line-height: 44px;
        color: var(--color-primary);
        text-align: center;
        transition: margin 0.25s ease-in-out, background-color 0.25s ease-in-out, color 0.25s ease-in-out, border-color 0.25s ease-in-out;
        will-change: margin;
    }
}