.team-list {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 30px;
  row-gap: 60px;
  margin-bottom: 80px;

  @include tablet {
    grid-template-columns: repeat(6, 1fr);
  }
}

.team-item {
  position: relative;
  grid-column: 3 span;
  margin: 0 auto;

  @include mobile {
    grid-column: 6 span;
  }

  @include hover {
    .team-item__socials {
      visibility: visible;
      opacity: 1;
    }
  }

  &__img {
    display: block;
    margin-bottom: 16px;
    border-radius: 4px;
  }

  &__socials {
    position: absolute;
    top: 280px;
    left: 0;
    @include flex-all-center;
    width: 100%;
    padding: 18.5px 0;
    border-radius: 0 0 4px 4px;
    background-color: rgba(255, 90, 48, 0.7);
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.25s ease-in-out, visibility 0.25s ease-in-out;
  }

  &__link {
    display: block;
    fill: var(--color-light);
    transition: fill 0.25s ease-in-out;

    &:not(:last-child) {
      margin-right: 12px;
    }

    @include hover {
      fill: var(--color-gray500);
    }
  }

  &__social {
    width: 20px;
    height: 20px;
    fill: inherit;
  }

  &__name {
    margin: 0;
    margin-bottom: 4px;
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    color: var(--color-gray800);
    text-align: center;
  }

  &__position {
    display: block;
    font-size: 16px;
    line-height: 160%;
    color: var(--color-gray700);
    text-align: center;
  }
}

.team-positions {
  margin-bottom: 120px;
  font-size: 18px;
  line-height: 150%;
  text-align: center;

  &__descr {
    margin-right: 2px;
    color: var(--color-gray900);
  }

  &__link {
    border-bottom: 1px solid currentColor;
    color: var(--color-primary);

    @include hover {
      color: var(--color-primary-hover);
    }
  }
}