.news-categories {
    padding-top: var(--section-offset);
    padding-bottom: 180px;

    &__title {
        margin-bottom: 40px;
    }
}

.news-tabs-nav {
    @include flex-v-center;
    margin-bottom: 60px;

    &__btn {
        display: block;
        width: 205px;
        height: 46px;
        border-radius: 4px;
        border: 1px solid transparent;
        font-weight: 700;
        font-size: 16px;
        line-height: 160%;
        color: var(--color-gray600);
        background-color: transparent;
        cursor: pointer;
        transition: color 0.25s ease-in-out;

        &--active {
            border-color: currentColor;
            color: var(--color-primary);
        }
    }
}

.news-categories__item {
    grid-column: 6 span;
    height: 482px;
}

.news-categories__pagination {
    @include flex-all-center;
    margin-top: 65px;
}

.news-pagination {
    &__item {
        display: block;
        font-weight: 700;
        font-size: 16px;
        line-height: 160%;
        color: var(--color-gray800);

        &:not(:last-child) {
            margin-right: 20px;
        }

        &:last-child {
            line-height: 0;
        }

        &--active {
            color: var(--color-primary);
        }
    }

    &__icon {
        width: 24px;
        height: 24px;
        color: inherit;
        fill: currentColor;
    }
}