.news-section {
    padding-bottom: var(--section-offset);

    &__title {
        margin-bottom: 59px;
    }

    &__container {
        display: grid;
        grid-template-columns: repeat(12, 1fr);
        grid-template-rows: initial;
        gap: 30px;

        @include mobile {
            grid-template-columns: repeat(4, 1fr);
        }
    }

    &__article {
        grid-column: 5 span;
        grid-row: 1 span;
        border-radius: 4px;
        box-shadow: var(--shadow-small);

        &--big {
            grid-column: 7 span;
            grid-row:  2 span;
        }

        @include big-tablet {
            grid-column: 12 span;
        }

        @include mobile {
            grid-column: 4 span;
        }
    }
}

.news {
    &__img {
        width: 100%;
        margin-bottom: 12px;
        border-radius: 4px 4px 0 0;

        &--big {
            margin-bottom: 20px;
        }
    }

    &__content {
        padding: 0 24px;
    }

    &__subtitle {
        margin: 0;
        margin-bottom: 8px;
        font-weight: 700;
        font-size: 20px;
        line-height: 150%;

        &--big {
            margin-bottom: 12px;
        }

        a {
            color: var(--color-gray900);
            transition: color 0.25s ease-in-out;

            @include hover {
                color: var(--color-primary);
            }

            &:focus {
                border-radius: 4px;
                outline: 2px solid var(--color-primary);
                outline-offset: 2px;
            }
        }
    }

    &__info {
        display: flex;
        flex-wrap: wrap;
        align-items: center;

        @include big-tablet {
            margin-bottom: 24px;
        }

        &--big {
            margin-bottom: 16px;
        }
    }

    &-info__item {
        display: flex;
        align-items: center;
        font-size: 14px;
        line-height: 150%;
        color: var(--color-gray700);

        &:not(:last-child) {
            &::after {
                content: '';
                display: block;
                width: 1px;
                height: 12px;
                margin: 0 12px;
                background-color: #787a80;
            }
        }

        &:last-child {
            &:before {
                content: "";
                display: block;
                width: 16px;
                height: 16px;
                margin-right: 8px;
                background-image: url("../img/svg/comment.svg");
            }
        }
    }

    &__text {
        margin: 0;
        margin-bottom: 26px;
        font-size: 16px;
        line-height: 160%;
        color: var(--color-gray800);
    }
}

.news-article {
    grid-column: 5 span;
    grid-row: 1 span;
    border-radius: 4px;
    box-shadow: var(--shadow-small);

    &__img {
        margin-bottom: 12px;
    }

    &__content {
        padding: 0 24px;
    }

    &__name {
        margin: 0;
        margin-bottom: 8px;
        font-weight: 700;
        font-size: 20px;
        line-height: 150%;
    }

    &__link {
        color: var(--color-gray900);
        transition: color 0.25s ease-in-out;

        @include hover {
            color: var(--color-primary);
        }
    }

    &__meta {
        display: flex;
        align-items: center;
        font-size: 14px;
        line-height: 150%;
        color: var(--color-gray700);
    }

    &__text {
        margin: 0;
        margin-top: 16px;
        font-size: 16px;
        line-height: 160%;
        color: var(--color-gray800);
    }

    &--big {
        grid-column: 7 span;
        grid-row: 2 span;

        &__img {
            margin-bottom: 20px;
        }

        &__name {
            margin-bottom: 12px;
        }
    }
}

.article-meta__item {
    @include flex-v-center;

    &:not(:last-child)::after {
        content: '';
        display: block;
        height: 12px;
        width: 1px;
        margin: 0 12px;
        background-color: var(--color-gray700);
    }

    &:last-child::before {
        content: '';
        display: block;

        width: 16px;
        height: 16px;
        margin-right: 8px;
        background-image: url("../../img/svg/comment.svg");
    }
}

.news-categories__container {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    column-gap: 30px;
    row-gap: 60px;
}

.categories-item {
    grid-column: 6 span;
    grid-row: auto;
    height: 482px;
}