.portfolio {
    padding-bottom: 100px;
}

.portfolio-tabs-nav {
    position: relative;
    top: -59px;
    @include flex-all-sb;
    margin-bottom: 61px;

    &__btn {
        @include flex-column-center;
        justify-content: center;
        width: 230px;
        height: 154px;
        padding: 0;
        border: none;
        border-radius: 4px;
        box-shadow: var(--shadow-small);
        color: var(--color-gray600);
        fill: var(--color-primary);
        background-color: var(--color-light);
        cursor: pointer;
        transition: color 0.25s ease-in-out, fill 0.25s ease-in-out, background-color 0.25s ease-in-out;

        &--active {
            color: var(--color-light);
            fill: var(--color-light);
            background-color: var(--color-primary);
            pointer-events: none;
        }

        @include hover {
            color: var(--color-gray900);
        }

        &:focus {
            outline: 2px solid var(--color-primary);
            outline-offset: 2px;
        }
    }

    &__ico {
        width: 48px;
        height: 48px;
        margin-bottom: 16px;
        fill: inherit;
        pointer-events: none;
    }

    &__caption {
        font-weight: 700;
        font-size: 16px;
        line-height: 160%;
        color: inherit;
        pointer-events: none;
    }
}

.portfolio-tabs {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 30px;

    &__item {
        display: none;
        grid-column: 4 span;

        &--visible {
            display: block;
        }

        &--visible-more {
            display: none;
        }
    }
}

.portfolio-item {
    display: block;
    border-radius: 4px;
    box-shadow: var(--shadow-small);
    overflow-y: hidden;

    @include hover {
        .portfolio-item__btn {
            margin-bottom: 24px;
        }
    }

    &:focus {
        outline: 2px solid var(--color-primary);
        outline-offset: 2px;
    }

    &__img {
        width: 100%;
        border-radius: 4px 4px 0 0;
    }

    &__content {
        padding: 13px 20px 0;
    }

    &__title {
        margin: 0;
        margin-bottom: 4px;
        font-weight: 700;
        font-size: 20px;
        line-height: 150%;
        color: var(--color-gray900);
        text-align: center;
    }

    &__descr {
        display: block;
        margin-bottom: 24px;
        font-size: 14px;
        line-height: 150%;
        text-align: center;
        color: var(--color-gray700);
    }

    &__btn {
        width: 172px;
        margin: 0 auto;
        margin-bottom: -46px;
        font-size: 14px;
        line-height: 44px;
        letter-spacing: 0.5px;
        text-align: center;
        color: var(--color-primary);
        will-change: margin;
        transition: margin 0.25s ease-in-out, color 0.25s ease-in-out, background-color 0.25s ease-in-out, border-color 0.25s ease-in-out;
    } 
}

.load-more {
    display: none;
    align-items: center;
    margin: 0 auto;
    margin-top: 59px;
    padding: 0;
    border: none;
    color: var(--color-gray800);
    background-color: transparent;
    cursor: pointer;
    transition: color 0.2s ease-in-out;

    @include hover {
        color: var(--color-primary);
    }

    &__icon {
        display: block;
        width: 24px;
        height: 24px;
        margin-right: 16px;
        color: inherit;
        fill: currentColor;
    }

    &__caption {
        display: block;
        font-weight: 700;
        font-size: 16px;
        line-height: 160%;
        color: inherit;
    }
}