.to-top {
    position: fixed;
    bottom: 50px;
    right: 30px;
    display: flex;
    align-items: flex-end;
    font-size: 14px;
    text-transform: uppercase;
    text-shadow: 0px 0px 20px #1e212c;
    color: var(--color-light);
    z-index: 1000;
    visibility: hidden;
    opacity: 0;
    transition: visibility 0.25s ease-in-out, opacity 0.25s ease-in-out;

    &__btn {
        @include flex-all-center;
        width: 40px;
        height: 40px;
        margin-left: 16px;
        border-radius: 4px;
        background-color: var(--color-primary);
        transform: rotate(90deg);
    }

    &__ico {
        width: 30px;
        height: 30px;
        fill: currentColor;
    }

    &--active {
        visibility: visible;
        opacity: 1;
    }

    &:focus {
        outline: 2px solid var(--color-primary);
        outline-offset: 2px;
    }
}

.footer {
    padding: 80px 0;
    color: rgba(255, 255, 255, 0.6);
    background-color: var(--color-gray900);

    &-container {
        position: relative;
        display: grid;
        grid-template-columns: repeat(12, 1fr);
        gap: 30px;

        @include small-tablet {
            grid-template-columns: repeat(6, 1fr);
        }
    }

    &__left {
        grid-column: 7 span;
        margin-bottom: 20px;

        @include small-tablet {
            grid-column: 6 span;
        }
    }

    &__top {
        display: flex;
        flex-wrap: wrap;
    }

    &__logo {
        margin-right: 60px;
        margin-bottom: 25px;
    }

    &__social {
        display: flex;
        margin-bottom: 25px;
    }

    &__descr {
        margin: 0;
        width: 100%;
        max-width: 495px;
        font-size: 14px;
        line-height: 150%;
    }

    &__form {
        grid-column: 5 span;
        margin-bottom: 20px;

        @include small-tablet {
            grid-column: 6 span;
        }
    }

    &__title {
        margin: 0;
        margin-bottom: 22px;
        font-weight: 700;
        font-size: 24px;
        line-height: 160%;
        color: var(--color-light);
    }

    &-form {
        &__items {
            display: flex;
            margin-bottom: 16px;
        }
    
        &__descr {
            margin: 0;
            font-size: 12px;
            line-height: 150%;
        }
    }

    &__input {
        width: 100%;
        max-width: 364px;
        padding-left: 16px;
        border-color: rgba(255, 255, 255, 0.2);
        border-radius: 4px 0 0 4px;
        font-size: 14px;
        background-color: rgba(255, 255, 255, 0.12);

        &::placeholder {
            color: var(--color-light);
            opacity: 0.6;
        }
    }

    &__btn {
        width: 131px;
        height: 44px;
        border-radius: 0 4px 4px 0;
    }

    &__subtitle {
        margin: 0;
        margin-bottom: 13px;
        font-weight: 700;
        font-size: 16px;
        line-height: 150%;
        letter-spacing: 1px;
        text-transform: uppercase;
        color: var(--color-light);
    }

    &__list {
        font-size: 16px;
        line-height: 160%;
    }

    &-list__item {
        &:not(:last-child) {
            margin-bottom: 4px;
        }

        a {
            color: inherit;
            transition: color 0.25s ease-in-out;

            @include hover {
                color: var(--color-light);
            }

            &:focus {
                outline: 2px solid var(--color-primary);
                outline-offset: 2px;
            }
        }
    }

    &__contacts {
        grid-column: 4 span;

        @include small-tablet {
            grid-column: 6 span;
        }
    }

    &-contacts__name {
        color: var(--color-light);
    }

    &__nav {
        grid-column: 8 span;
        display: flex;
        padding-left: 56px;

        @include small-tablet {
            grid-column: 6 span;
            flex-wrap: wrap;
            padding-left: 0;
        }
    }

    &-nav {
        &__col:not(:last-child) {
            margin-bottom: 30px;
            margin-right: 128px;
        }
    }
}

.social {
    &__item {

        &:not(:last-child) {
            margin-right: 16px;
        }
    }

    &__link {
        display: block;
        width: 24px;
        height: 24px;

        @include hover {
            .social__ico {
                opacity: 1;
            }
        }

        &:focus {
            outline: 2px solid var(--color-primary);
            outline-offset: 2px;
        }
    }

    &__ico {
        display: block;
        width: 100%;
        height: 100%;
        fill: var(--color-light);
        opacity: 0.6;
        transition: opacity 0.25s ease-in-out;
    }
}