.contacts-section {
    background-image: url(../img/contacts-section-bg.jpg);
    @include bg-cover;
    padding: 80px 0;

    &__form {
        width: 100%;
        height: 100%;
        max-width: 495px;
        min-height: 666px;
        margin-left: auto;
        padding: 48px 40px;
        border-radius: 4px;
        background-color: var(--color-light);
    }

    &__title {
        margin: 0;
        margin-bottom: 25px;
        font-weight: 700;
        font-size: 28px;
        line-height: 150%;
        color: var(--color-gray900);
        text-align: center;

        @include mobile {
            font-size: 25px;
        }
    }

    &__label {
        @include flex-column;
        margin-bottom: 20px;

        @include mobile {
            margin-bottom: 15px;
        }
    }

    &__designation {
        display: block;
        margin-bottom: 8px;
        font-size: 14px;
        line-height: 150%;
        color: var(--color-gray800);

        @include mobile {
            margin-bottom: 5px;
        }
    }

    &__input {
        width: 100%;
        height: 44px;
        padding-left: 16px;
        font-size: 14px;

        @include mobile {
            height: 38px;
        }
    }

    &__textarea {
        width: 100%;
        height: 66px;
        padding: 11px 0 0 16px;
        font-size: 14px;
        resize: none;
    }

    &__btn {
        display: block;
        width: 174px;
        height: 44px;
        margin: 0 auto;
        margin-top: 24px;
        font-size: 14px;
        letter-spacing: 0.5px;
    }
}