.hero {
    &--secondary {
        padding-top: 152px;
        background-color: var(--color-gray200);
        background-position: top right;
        background-repeat: no-repeat;

        &__nav {
            display: flex;
        }

        &__title {
            margin: 0;
            margin-bottom: 24px;
            font-weight: 700;
            font-size: 72px;
            line-height: 130%;
            letter-spacing: 1px;
            text-transform: uppercase;
            color: var(--color-gray900);

            @include mobile {
                font-size: 48px;
            }
        }

        &__article {
            width: 100%;
            max-width: 600px;
            font-size: 20px;
            line-height: 150%;
            color: var(--color-gray700);
        }
    }

    &-nav {
        &__item {
            @include flex-v-center;
            margin-bottom: 40px;
            color: var(--color-gray800);
    
            &:not(:last-child)::after {
                content: '/';
                margin: 0 8px;
                font-size: 14px;
                color: var(--color-gray600);
            }
    
            &:last-child {
                color: var(--color-gray600);
            }
        }
    
        &__link {
            display: block;
            font-size: 14px;
            line-height: 150%;
            color: inherit;
            transition: color 0.25s ease-in-out;
    
            @include hover {
                color: var(--color-primary);
            }
        }
    }

    &--main {
        padding-top: 221px;
        padding-bottom: 353px;
        background-image: url('../img/hero-bg.jpg');
        @include bg-cover;
        color: var(--color-light);

        &__title {
            margin: 0;
            margin-bottom: 24px;
            font-weight: 700;
            font-size: 72px;
            line-height: 130%;
            letter-spacing: 1px;
            text-transform: uppercase;
    
            span {
                color: var(--color-primary);
            }
    
            @include small-tablet {
                font-size: 64px;
            }
    
            @include mobile {
                font-size: 35px;
            }
        }
    }

    &__article {
        width: 100%;
        max-width: 595px;
        margin-bottom: 61px;
        font-size: 20px;
        line-height: 150%;

        @include mobile {
            font-size: 16px;
        }
    }

    &__buttons {
        @include flex-v-center;
        flex-wrap: wrap;
    }

    &__button {
        padding: 0 39px;
        font-weight: 700;
        font-size: 16px;
        line-height: 50px;
        letter-spacing: 0.5px;
        color: var(--color-light);
        
        &:not(:last-child) {
            margin-right: 22px;

            @include mobile {
                margin-right: 0;
                margin-bottom: 20px;
            }
        }
    }
}

