.header {
    background-color: var(--color-light);

    &--page {
        position: absolute;
        width: 100%;
        background-color: transparent;
    }

    &-container {
        @include flex-all-sb;
        padding: 22px 15px;
    }

    &__logo {
        &:focus {
            border-radius: 4px;
            outline: 2px solid var(--color-primary);
            outline-offset: 2px;
        }

        @include big-tablet {
            max-width: 100px;
        }

        @include tablet {
            max-width: 100%;
        }
    }

    &-nav {
        display: block;

        @include tablet {
            position: fixed;
            top: 0;
            left: 0;
            z-index: 1000;
            width: 80%;
            height: 100vh;
            padding: 50px;
            overflow-y: auto;
            background-color: var(--color-light);
            transform: translateX(-150%);
            transition: transform 0.25s ease-in-out;
        }

        @include small-tablet {
            padding-left: 25px;
        }

        &--active {
            transform: translateX(0);
        }
    }

    &__nav-list {
        @include flex-v-center;
        margin-left: -110px;
        padding: 0;

        @include big-tablet {
            margin-left: 0;
        }

        @include tablet {
            @include flex-column;
            align-items: flex-start;
            margin-bottom: 40px;
        }
    }

    &__nav-item {
        font-weight: 700;
        font-size: 16px;
        line-height: 160%;

        &:not(:last-child) {
            margin-right: 41px;

            @include big-tablet {
                margin-right: 30px;
            }

            @include tablet {
                margin-right: 0;
                margin-bottom: 30px;
            }
        }
    }

    &__nav-link {
        color: var(--color-gray800);
        transition: color 0.25s ease-in-out;

        @include hover {
            color: var(--color-primary);
        }

        &:focus {
            border-radius: 4px;
            outline: 2px solid var(--color-primary);
            outline-offset: 2px;
        }

        &--active {
            color: var(--color-primary);
            pointer-events: none;
        }
    }

    &-contact {
        @include flex-v-center;
        padding: 0;

        @include tablet {
            display: none;
        }

        &--nav {
            display: none;

            @include tablet {
                display: block;
            }
        }

        &__item {
            &:not(:last-child) {
                margin-right: 36px;
            }

            @include tablet {
                &:first-child {
                    margin-bottom: 30px;
                }
            }
        }

        &__link {
            @include flex-column;
            padding-left: 52px;
            background-repeat: no-repeat;
            background-position: left center;
            background-size: 40px 40px;

            &--phone {
                background-image: url('../img/svg/header-phone.svg');
            }
            
            &--mail {
                background-image: url('../img/svg/header-mail.svg');
            }

            @include hover {
                .header-contact__value {
                    color: var(--color-primary);
                }
            }

            &:focus {
                border-radius: 4px;
                outline: 2px solid var(--color-primary);
                outline-offset: 2px;
            }
        }

        &__descr {
            font-weight: 700;
            font-size: 14px;
            line-height: 150%;
            color: var(--color-gray800);
        }

        &__value {
            font-size: 18px;
            line-height: 150%;
            color: var(--color-gray900);
            transition: color 0.25s ease-in-out;
        }
    }
}