.achieves {
  padding-top: var(--section-offset);
  padding-bottom: 240px;
}

.achieves-list {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 30px;
  margin-bottom: 141px;

  @include tablet {
    grid-template-columns: repeat(6, 1fr);
  }
}

.achieves-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-column: 3 span;
  padding-top: 70px;
  text-align: center;
  background-repeat: no-repeat;
  background-position: top center;
  background-size: 48px 48px;

  @include mobile {
    grid-column: 6 span;
  }

  &--recommend {
    background-image: url("../img/svg/recommend.svg");
  }

  &--painting {
    background-image: url("../img/svg/painting.svg");
  }

  &--helmet {
    background-image: url("../img/svg/helmet.svg");
  }

  &--pantone {
    background-image: url("../img/svg/pantone.svg");
  }

  &__value {
    display: block;
    margin-bottom: 8px;
    font-weight: 700;
    font-size: 46px;
    line-height: 130%;
    color: var(--color-primary);

    @include big-tablet {
      font-size: 36px;
      line-height: 54px;
    }
  }

  &__name {
    display: block;
    font-size: 14px;
    line-height: 150%;
    color: var(--color-gray900);
  }
}

.achieves-content {
  display: flex;

  @include tablet {
    display: block;
  }

  &__img {
    position: relative;
    top: 120px;
    display: block;
    border-radius: 4px;

    @include big-tablet {
      max-width: 45%;
    }

    @include tablet {
      top: 0;
      max-width: 100%;
    }
  }

  &__info {
    position: relative;
    left: -30px;
    width: 735px;
    padding: 80px 105px 71px 135px;
    border-radius: 4px;
    box-shadow: var(--shadow-small);
    flex-shrink: 0;
    background-color: var(--color-light);
    z-index: 10;

    @include big-tablet {
      left: 0;
      width: 55%;
    }

    @include tablet {
      width: 100%;
    }

    @include mobile {
      padding: 15px;
    }
  }
  
  &__quote {
    position: relative;
    margin: 0;

    @include mobile {
      padding-top: 40px;
      padding-bottom: 20px;
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: -58px;
      display: block;
      width: 28px;
      height: 20px;
      background-image: url("../img/svg/quotes.svg");
      background-repeat: no-repeat;

      @include mobile {
        left: 0;
      }
    }
  }
}

.quote {
  &__text {
    margin: 0;
    margin-bottom: 30px;
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    color: var(--color-gray900);
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 50px;
  }

  &__autor-name {
    display: block;
    margin-bottom: 4px;
    font-weight: 700;
    font-size: 16px;
    line-height: 160%;
    color: var(--color-gray900);
  }

  &__autor-position {
    display: block;
    font-size: 14px;
    line-height: 150%;
    color: var(--color-gray700);
  }
}